.App{
    margin:0;
    padding:0;
    max-width:100vw;
}
@media only screen and (min-width: 1300px) {
    .Top{
        display:grid;
        grid-template-rows: 1fr 1fr;
        width:100%;

    }
    .Bot{
        display:grid;
        grid-template-rows: 1fr;
        width:100%;
    }
}
@media only screen and (600px <= width <= 1300px) {
    .Top{
        display:grid;
        grid-template-rows: 0.5fr 0.5fr;
        width:100%;

    }
    .Bot{
        display:grid;

        grid-template-rows: 1fr;
        width:100%;
    }
}

@media only screen and (max-width: 600px) {
    .Top{
        display:grid;
        grid-template-rows: 0.5fr 0.5fr;
        width:100%;
    }
    .Bot{
        display:grid;

        width:100%;

    }
}